<template>
  <div>
    <button
      type="button"
      :class="[classesType, classes]"
      :disabled="isButtonDisable"
      @click="$emit('click')"
    >
      <!-- v-if="!dropdown" -->
      <fp-icon
        v-if="icon || circle || loading"
        :name="defaultIcon"
        :class="iconClass"
        :size="iconSize"
        class="button__icon"
      />

      <span v-if="!circle" :class="{ 'button-content--loading': loading }">
        <slot name="default" />
      </span>

      <span v-if="loading" class="button__loading-icon">
        <svg viewBox="0 0 24 24">
          <g>
            <path
              fill="currentColor"
              fill-opacity=".9"
              fill-rule="evenodd"
              d="M3.267 8.157a12.307 12.307 0 013-3.389c1.73-1.35 3.759-2.196 5.732-2.197a9.466 9.466 0 018.86 6.21.857.857 0 001.61-.59A11.18 11.18 0 0012.001.857H12c-2.451 0-4.842 1.04-6.788 2.56A14.109 14.109 0 001.983 6.97L1.69 5.792a.857.857 0 10-1.663.416L.88 9.629a.857.857 0 001.054.628l3.416-.854a.857.857 0 10-.416-1.663l-1.668.417zm19.855 6.225a.857.857 0 00-1.055-.64l-3.418.855a.857.857 0 00.416 1.663l1.726-.431a10.745 10.745 0 01-8.814 5.6 9.467 9.467 0 01-8.836-6.21.857.857 0 10-1.61.59 11.18 11.18 0 0010.467 7.334l.002-.857.048.855a12.458 12.458 0 009.988-6.035l.275 1.102a.857.857 0 101.663-.416l-.852-3.41z"
              clip-rule="evenodd"
            />
          </g>
        </svg>
      </span>
    </button>
  </div>
</template>

<script>
import "../../../assets/scss/button.scss";
import fpIcon from "../Icon/index.vue";
export default {
  name: "fpButton",
  components: {
    fpIcon,
  },
  props: {
    fluid: {
      type: Boolean,
      default: false,
    },

    size: {
      type: String,
      default: "medium",
      validator(value) {
        return ["tiny", "small", "medium", "large"].indexOf(value) !== -1;
      },
    },

    styles: {
      type: String,
      default: "primary",
      validator(value) {
        return (
          [
            "primary",
            "secondary",
            "tertiary",
            "success",
            "danger",
            "warning",
            "formpro",
            "formpro--outline",
            "outline",
          ].indexOf(value) !== -1
        );
      },
    },

    disabled: {
      type: Boolean,
      default: false,
    },

    circle: {
      type: Boolean,
      default: false,
    },

    icon: {
      type: String,
      default: null,
    },

    iconPosition: {
      type: String,
      default: "left",
      validator(value) {
        return ["left", "right"].indexOf(value) !== -1;
      },
    },

    loading: {
      type: Boolean,
      default: false,
    },

    // dropdownMenu: {
    //   type: String,
    // },
    type: {
      type: String,
      default: "button",
      validator(value) {
        return ["button", "link"].indexOf(value) !== -1;
      },
    },
  },
  computed: {
    classes() {
      return {
        [`${this.type === "link" ? "button-link" : "button"}--${
          this.styles
        }`]: true,
        [`button--${this.size}`]: true,
        "button--fluid": this.fluid,
        "button--circle": this.circle,
        loading: this.loading,
      };
    },
    classesType() {
      return {
        [`${this.type === "link" ? "button-link" : "button"}`]: true,
      };
    },
    defaultIcon() {
      if (!this.icon && this.circle) {
        return "add";
      }
      return this.icon;
    },
    iconSize() {
      switch (this.size) {
        case "tiny":
          return 12;

        case "large":
          return 20;

        default:
          return 16;
      }
    },
    isButtonDisable() {
      return this.disabled || this.loading;
    },
    iconClass() {
      return {
        [`is-icon-${this.iconPosition}`]: true,
      };
    },
  },
};
</script>
