<template>
  <div
    class="form-group form-group--label "
    :disabled="disabled"
    @click="inputFocus"
  >
    <span v-if="icon" class="form-group__addon addon-left">
      <fp-icon :name="icon" />
    </span>

    <div
      class="input-group"
      :class="{
        inner: innerPlaceholder,
        active: innerPlaceholder && value && value.length > 0,
        filled: value && value.length > 0,
      }"
    >
      <fp-label v-if="placeholder" class="input-group-placeholder">
        {{ placeholder }}
      </fp-label>

      <input
        ref="inputBox"
        class="form-control"
        :name="name"
        :type="inputType"
        :disabled="disabled"
        :value="value"
        :readonly="readonly"
        autocomplete="off"
        @input="$emit('input', $event.target.value)"
        @click="$emit('click', $event.target.value)"
        @focus="placeholderStatus = true"
        @blur="placeholderStatus = false"
      />
    </div>

    <span class="form-group__addon addon-right">
      <button
        v-if="type == 'password'"
        class="
          button-link button-link--primary
          button--small button--circle
          show-password
        "
        @click="switchPassword"
      >
        <svg
          v-if="passwordShow"
          width="16"
          height="16"
          viewBox="0 0 24 24"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
