          </g>
        </svg>
      </button>
    </span>
  </div>
</template>

<script>
import "../../../assets/scss/input.scss";
import fpIcon from "../Icon/index.vue";
import fpLabel from "./Label.vue";
import fpLogin from "../../../assets/scss/login.scss"

export default {
  name: "fpInput",
  components: {
    fpIcon,
    fpLabel,
    fpLogin

  },
  props: {
    name: {
      type: String,
      default: "input",
    },
    disabled: {
      type: Boolean,
    },
    readonly: {
      type: Boolean,
    },
    value: {
      type: [String, Number],
      default: "",
    },
    placeholder: {
      type: String,
      default: "Value",
    },
    innerPlaceholder: {
      type: Boolean,
    },
    type: {
      type: String,
      default: null,
    },
    icon: {
      type: String,
      default: null,
    },
  },
  data() {
    return {
      placeholderStatus: false,
      passwordShow: false,
      text: "",
    };
  },
  computed: {
    inputType() {
      if (this.type === "password" && !this.passwordShow) {
        return "password";
      }
      if (this.type === "password" && this.passwordShow) {
        return "text";
      }
      return this.type;
    },
  },
  methods: {
    switchPassword() {
      this.passwordShow = !this.passwordShow;
    },
    inputFocus() {
      this.$refs.inputBox.focus();
    },
  },
};
</script>
