<template>
  <div :class="{ ...wrapperClasses }" class="upluaderPosition">
    <div class="uploader " :class="classes">
      <div class="uploader__wrapper  ">
        <span
          v-cloak
          :ref="`${name}-file`"
          class="uploader__container uploader__"
          tabindex="0"
          @dragover.prevent="debounced.dragover"
          @dragleave.prevent="debounced.dragleave"
          @dragenter.prevent
          @dragstart.prevent
          @drop="drop"
        >
          <span v-show="loading" class="uploader__loading">
            <loader
              class="uploader__loading--bar"
              type="finitive"
              :total="loaderTotal"
              :loaded="loaderLoaded"
              :hide-text="type === 'horizontal'"
              :only-percentage="type === 'vertical'"
            />
          </span>
          <span v-if="files.length" class="uploader__files">
            <span v-for="(file, i) in files" :key="i" class="uploader__file">
              <span class="uploader__file--cover">
                <img
                  v-if="file.data.icon"
                  :src="file.data.icon"
                  :alt="file.data.name"
                />
                <img v-else :src="file.src" :alt="file.data.name" />
              </span>
              <span class="uploader__file--container">
                <span class="uploader__file--content">
                  <span class="uploader__file--content-name">
                    {{ file.data.name }}
                  </span>
                  <span class="uploader__file--content-size">
                    {{ humanFileSize(file.data.size) }}
                  </span>
                </span>
                <fp-icon circle type="link" icon="close" @click="remove(file)">
                </fp-icon>
              </span>
            </span>
          </span>
          <label>
            <input
              :ref="`${name}-file-input`"
              type="file"
              :name="name"
              :multiple="multiple"
              :accept="accept"
              @change="onChange"
            />
            <span class="uploader__input">
              <span
                v-show="dragEnter"
                data-here
                class="uploader__input--drop-here"
              >
                <span>Drop file now</span>
              </span>
              <fp-icon
                name="file-upload"
                @click="$refs[`${name}-file-input`].click()"
              >
              </fp-icon>
              <span class="uploader__input--drag-text">
                <template v-if="type === 'horizontal'"
                  >Drag files here</template
                >
                <template v-else
                  >Choose a file or drag it here
                  <div>
                    We require both front and back page of the car papers
                  </div>
                 
                  <div>
                    Supported Types: JPG, PNG .Max Size : 20MB
                  </div>
                </template>
              </span>
              <!-- <span class="uploader__input--or-text">or</span> -->
            </span>
          </label>
          <span v-if="$scopedSlots.info" class="uploader__info">
            <slot name="info" />
          </span>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import "../../../assets/scss/uploader.scss";
import Loader from "../Loader/index.vue";
import fpIcon from "../Icon/index.vue";

const debounce = require("lodash.debounce");

export default {
  name: "fpUploader",
  
  components: {
    Loader,
    fpIcon,
  },
  props: {
    name: {
      type: String,
      default: "uploader",
    },
    type: {
      type: String,
      default: "horizontal",
      validator(value) {
        return ["horizontal", "vertical"].indexOf(value) !== -1;
      },
    },
    disabled: {
      type: Boolean,
    },
    loading: {
      type: Boolean,
    },
    loaderTotal: {
      type: Number,
      default: 0,
    },
    loaderLoaded: {
      type: Number,
      default: 0,
    },
    accept: {
      type: String,
      default: "*",
    },
    multiple: {
      type: Boolean,
    },
  },
  data() {
    return {
      files: [],
      debounced: {
        dragover: debounce(this.dragover),
        dragleave: debounce(this.dragleave, 250),
      },
      dragEnter: false,
      extensionList: {
        normal: ["doc", "pdf", "xls"],
        video: ["flv", "mov", "webm", "mkv", "avi", "wmv", "mp4"],
      },
    };
  },
  computed: {
    wrapperClasses() {
      return {
        disabled: this.disabled,
      };
    },
    classes() {
      return {
        [`uploader--${this.type}`]: true,
        "uploader--dragging": this.dragEnter,
        "uploader--loading": this.loading,
        "uploader--file-list": this.files.length,
      };
    },
  },
  watch: {
    files(files) {
      console.log(files, "uploader");
      this.$emit("files", files);
    },
  },
  created() {},
  methods: {
    dragover() {
      this.dragEnter = true;
    },
    dragleave() {
      this.dragEnter = false;
    },
    addImageSource(fileList) {
      return fileList.map((file) => {
        const ext = file.name.split(".").pop();
        let icon = null;
        if (!file.type.startsWith("image/")) {
          if (this.extensionList.normal.includes(ext)) {
            icon = `/assets/icons/file/${ext}.svg`;
          } else if (this.extensionList.video.includes(ext)) {
            icon = "/assets/icons/file/video.svg";
          } else {
            icon = "/assets/icons/file/unknown.svg";
          }
        }
        return {
          data: {
            size: file.size,
            name: file.name,
            type: file.type,
            ext,
            icon,
          },
          src: URL.createObjectURL(file),
        };
      });
    },
    onChange(getClicked) {
      let files = [];
      if (getClicked) {
        files = [...getClicked.target.files];
      } else {
        files = [...this.$refs[`${this.name}-file`].files];
      }

      if (files.length > 0) {
        if (this.multiple) {
          this.files.push(...this.addImageSource([...files]));
          // this.files = this.addImageSource([...files])
        } else {
          this.files = this.addImageSource([files[0]]);
        }
      } else {
        this.files = [];
      }

      this.dragEnter = false;
    },
    drop(event) {
      event.preventDefault();
      this.$refs[`${this.name}-file`].files = event.dataTransfer.files;
      this.onChange();
    },
    remove(i) {
      this.files.splice(i, 1);
    },
    humanFileSize(bytes, dp = 1) {
      const thresh = 1000;

      if (Math.abs(bytes) < thresh) {
        return `${bytes} B`;
      }

      const units = ["Kb", "Mb", "Gb", "Tb", "Pb", "Eb", "Zb", "Yb"];
      let u = -1;
      const r = 10 ** dp;

      do {
        bytes /= thresh;
        u += 1;
      } while (
        Math.round(Math.abs(bytes) * r) / r >= thresh &&
        u < units.length - 1
      );

      return `${bytes.toFixed(dp)} ${units[u]}`;
    },
  },
};
</script>
