<template>
  <div class="home">
    <form @submit.prevent="login">
    <fp-input class="form-group-login" placeholder="Username" v-model="form.username" ></fp-input>
    <fp-input class="form-group-login" placeholder="Password" type="password"  v-model="form.password" ></fp-input>
        <fp-button @click="login" class="button-login" iconPosition="right"  styles="formpro" icon="arrow-right">
          Login
        </fp-button>
    </form>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios';
export default {
  name: 'Login',
  created(){
    // if(User.loggedIn()){
    //   this.$router.push({name: 'home'})
    // }
  },

  data(){
    return {
      form:{
        username: null,
        password: null
      },
      errors:{}
    }
  },
  methods:{
    login(){
      axios.post('https://support.balinsoft.com/docread/public/api/login_check',this.form
          )
          .then(res => {
           
            this.$router.push({ name: 'About' })
           
          })

          .catch(error => this.errors = error.response.data)
         
    }
  }
}
</script>
