<template>
  <div class="loader" :class="classes">
    <template>
      <fp-icon v-if="type === 'infinitive'" icon="refresh-preperad" />
      <template v-else>
        <div class="loader--line__bg">
          <div
            class="loader--line__fill"
            :style="{ width: `${percentage}%` }"
          />
        </div>
        <div v-if="check" class="loader--info">
          <div class="loader--info__percentage">%{{ percentage }}</div>
          <div v-if="!onlyPercentage" class="loader--info__operation">
            <span>{{ loadedText }}</span>
            <span>/ {{ totalText }}</span>
          </div>
        </div>
      </template>
    </template>
  </div>
</template>

<script>
import "../../../assets/scss/loader.scss";
import fpIcon from "../Icon/index.vue";

export default {
  name: "fpLoader",
  components: {
    fpIcon,
  },
  props: {
    type: {
      type: String,
      default: "infinitive",
      validator(value) {
        return ["finitive", "infinitive"].indexOf(value) !== -1;
      },
    },
    total: {
      type: Number,
      default: 0,
    },
    loaded: {
      type: Number,
      default: 0,
    },
    prefix: {
      type: String,
      default: null,
    },
    suffix: {
      type: String,
      default: null,
    },
    hideText: {
      type: Boolean,
      default: false,
    },
    onlyPercentage: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    classes() {
      return {
        [`loader--${this.type}`]: true,
      };
    },
    check() {
      return this.loaded && this.total && !this.hideText;
    },
    percentage() {
      if (this.loaded && this.total) {
        const percentage = Math.floor((this.loaded / this.total) * 100);
        if (percentage > 100) {
          return 100;
        }
        if (percentage < 0) {
          return 0;
        }
        return percentage;
      }
      return 0;
    },
    loadedText() {
      const adds = this.getAdds();
      return `${adds.prefix}${this.loaded}${adds.suffix}`;
    },
    totalText() {
      const adds = this.getAdds();
      return `${adds.prefix}${this.total}${adds.suffix}`;
    },
  },
  methods: {
    getAdds() {
      return {
        prefix: this.prefix ? `${this.prefix} ` : "",
        suffix: this.suffix ? ` ${this.suffix}` : "",
      };
    },
  },
};
</script>
